/*
for : January 2020
input -> 0 , 2020 
*/
const getMonthInfo = (month, year) => {
    if (month >= 0 && month <= 11 && year > 0) {
        let monthInfo = allMonths[month];

        if (month == 1 && year % 4 == 0) {
            monthInfo.noOfDays = 29;
        }

        return monthInfo;
    }
    else {
        throw Error();
    }
}


const getWeekdayInfo = (someDay) => {
    return allWeekdays[someDay.getDay()];
}

